* {
   margin: 0;
   padding: 0;
   box-sizing: 0;
}

#root{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App {
    width:800px;
}

.post{
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
}

.post-enter {
  transform: translateX(-350px);
}
.post-enter-active {
  transform: translateX(0px);
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  transform: translateX(-350px);
  transition: all 500ms ease-in;
}

.page__wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
}

.page{
  margin-top: 20px;
  margin-right:10px;
  margin-left: 10px;
  padding: 10px;
  border: 1px solid teal;
  cursor: pointer;
}

.page__current{
    border: 2px solid teal;
    font-weight: bold;
}

.navbar{
  height: 50px;
  width: 100vw;
  display: flex;
  align-items: center;
  padding: 0 15px;
  background-color: lightgrey;
}

.navbar__links{
  margin-left: auto;
}